import { useState } from 'react';
import styled from '@emotion/styled';
import { push, ref, serverTimestamp } from 'firebase/database';
import { realtimeDb } from 'src/libs/firebase';

const guestbookRef = ref(realtimeDb, 'guestbook');

const CommentForm = () => {
    const [name, setName] = useState<string>('');
    const [message, setMessage] = useState<string>('');
    const [guests, setGuests] = useState<string>('');
    const [accompanyingInvitees, setAccompanyingInvitees] = useState<string>('');
    const [attendanceStatus, setAttendanceStatus] = useState<'attending' | 'not attending'>('attending');

    const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        if (!name || !message || !attendanceStatus) {
            alert('Please fill in all the fields 🥹');
        } else {
            e.preventDefault();
            const guestbookMessage = {
                name: name,
                message: message,
                guests: guests,
                accompanyingInvitees: accompanyingInvitees,
                attendanceStatus: attendanceStatus,
                createdAt: serverTimestamp(),
                date: new Date().toLocaleString(),
            };
            void push(guestbookRef, guestbookMessage);

            alert('Message Sent 💌');
            setName('');
            setMessage('');
            setGuests('');
            setAccompanyingInvitees('');
            setAttendanceStatus('attending');
        }
    };

    return (
        <FormWrapper onSubmit={handleSubmit}>
            <NameInput
                placeholder="Name"
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
            />
            <MessageInput
                placeholder="Message"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
            />
            <AccompanyingInviteesInput
                placeholder="Accompanying Invitees"
                type="text"
                value={accompanyingInvitees}
                onChange={(e) => setAccompanyingInvitees(e.target.value)}
            />

            <GuestsInput
                placeholder="Guests"
                type="text"
                value={guests}
                onChange={(e) => setGuests(e.target.value)}
            />
            <StatusSelect
                value={attendanceStatus}
                onChange={(e) => setAttendanceStatus(e.target.value as 'attending' | 'not attending')}
            >
                <option value="attending">Attending</option>
                <option value="not attending">Not Attending</option>
            </StatusSelect>
            <SubmitButton type="submit">Submit</SubmitButton>
        </FormWrapper>
    );
};

const FormWrapper = styled.form`
    display: flex;
    flex-direction: column;
    gap: 2px;
    overflow: visible;
    align-items: center;
`;

const NameInput = styled.input`
    width: 100%;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 4px;
    font-size: 1rem;
    line-height: 1;
    outline: none;
    border: 1px solid #ccc;
    font-family: inherit;
    font-weight: 300;
`;

const MessageInput = styled.textarea`
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 4px;
    font-size: 1rem;
    line-height: 1.5;
    outline: none;
    border: 1px solid #ccc;
    resize: none;
    font-family: inherit;
    font-weight: 300;
`;

const AccompanyingInviteesInput = styled.input`
    width: 100%;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 4px;
    font-size: 1rem;
    line-height: 1;
    outline: none;
    border: 1px solid #ccc;
    font-family: inherit;
    font-weight: 300;
`;

const GuestsInput = styled.input`
    width: 100%;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 4px;
    font-size: 1rem;
    line-height: 1;
    outline: none;
    border: 1px solid #ccc;
    font-family: inherit;
    font-weight: 300;
`;

const StatusSelect = styled.select`
  width: 100%;
  padding: 4px;
  border-radius: 4px;
  font-size: 1rem;
  line-height: 1;
  border: 1px solid #ccc;
  font-family: inherit;
  font-weight: 300;
`;

const SubmitButton = styled.button`
  width: 100%;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 1rem;
  line-height: 1.5;
  border: 1px solid lightgray;
  background-color: white;
  font-family: inherit;
  font-weight: inherit;
  color: inherit;
`;

export default CommentForm;
